.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    padding: 30px;

    .modal-content {
        background-color: #202027;
        padding: 20px;
        border-radius: 16px;
        min-width: 300px;
        max-width: 600px;

        .modal-image-container {
            margin-bottom: 20px;
            padding: 0 30px;
            text-align: center;

            img {
                width: auto; // or specific size e.g. 80px
                height: auto; // maintain aspect ratio
                max-width: 100%; // ensure it is not larger than the container
            }
        }
        .modal-title {
            margin-bottom: 20px;
            font-size: 16px;
            font-weight: 600;
            color: #fff;
            text-align: center;
        }
        .modal-body {
            margin-bottom: 20px;
            p {
                font-size: 14px;
                line-height: 20px;
                color: #a9a9a9;
                text-align: center;
                span {
                    font-size: 12px;
                    color: #efefef;
                }
                &.explain {
                    font-size: 11px;
                    margin-top: 20px;
                }
            }
        }

        .modal-footer {
            display: flex;
            justify-content: center;
            background-color: #6058eb;
            border-radius: 9px;
            button {
                width: 100%;
                padding: 14px;
                color: #fff;
                font-size: 15px;
                font-weight: 400;
            }
        }
    }
}
