.indicator {
    display: flex;
    justify-content: center;
    align-items: center;

    .spinner {
        border: 2px solid rgba(255, 255, 255, 0.1); // 변경: 보다 세미 투명한 테두리
        width: 20px; // 변경: 원하는 크기에 맞게 조정
        height: 20px; // 변경: 원하는 크기에 맞게 조정
        border-radius: 50%;
        border-top: 2px solid #fff;
        border-right: 2px solid #fff;
        animation: spin 1s linear infinite;
    }

    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
}
